.footer {
  background: #273E51;
  padding: 30px 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    margin-bottom: 30px;
    &:not(:last-child) {
      margin-right: 30px;
    }
  }
  &__logo {
    display: flex;
    align-items: center;
    &-img {
      width: 100px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      letter-spacing: 1.4px;
      text-transform: uppercase;
      color: #FFF;
      margin-left: 12px;
    }
  }
  &__numbers {
    margin-top: 20px;
  }
  &__number {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 120%;
    &:not(:first-child) {
      margin-top: 5px;
    }
    span {
      min-width: 130px;
      text-align: end;
      color: #888;
    }
    a {
      color: #F7F8FA;
      margin-left: 3px;
      &:hover {
        color: #FF5757;
      }
    }
  }
  &__btn {
    width: 100%;
    height: 45px;
    background: transparent;
    border: 1px solid #D9D9D9;
    margin-top: 20px;
    span {
      font-size: 16px;
      line-height: 120%;
      color: #F7F8FA;
    }
    &:hover {
      border: 1px solid #FF2B2B;
      background: #FF2B2B;
    }
  }
  &__nav {
    &-title {
      font-size: 20px;
      line-height: 120%;
      font-weight: 600;
      color: #ffffff;
    }
  }
  &__list {
    margin-top: 15px;
    li {
      font-size: 15px;
      line-height: 120%;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 15px;
      }
      a:hover {
        color: #FF5757;
      }
    }
  }
  &__info {
    display: flex;
    align-items: center;
    margin-top: 30px;
    &-icon {
      flex-shrink: 0;
      width: 37px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      margin-left: 11px;
    }
  }
  &__account {
    display: flex;
    align-items: center;
    margin-top: 45px;
    &-icon {
      width: 21px;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.5s;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      margin-left: 9px;
      transition: all 0.5s;
    }
    &:hover {
      .footer__account-icon svg path {
        stroke: #FF5757;
      }
      .footer__account-text {
        color: #FF5757;
      }
    }
  }
  &__development {
    display: flex;
    align-items: center;
    font-size: 12px;
    line-height: 120%;
    color: #ffffff;
    margin-top: 45px;
    a {
      text-decoration-line: underline;
      text-underline-offset: 3px;
      margin-left: 3px;
      margin-top: -2px;
      &:hover {
        color: #FF5757;
      }
    }
  }
  &__info2 {
    font-size: 12px;
    line-height: 120%;
    color: rgba(110, 125, 143, 0.80);
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__items {
      display: block;
    }
    &__item {
      margin: 0;
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 22px;
      }
    }
    &__btn {
      width: 267px;
      margin-left: auto;
      margin-right: auto;
    }
    &__info {
      justify-content: center;
      margin-top: 45px;
    }
    &__account {
      justify-content: center;
    }
    &__development {
      justify-content: center;
    }
    &__info2 {
      margin-top: 45px;
    }
  }
}