.main {
  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 280px 0 80px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &__left {
    width: 740px;
    margin-right: 30px;
    position: relative;
    z-index: 2;
  }
  &__right {
    width: calc(100% - 770px);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    .main__text {
      display: none;
    }
  }
  &__title {
    font-size: 64px;
    line-height: 105%;
    font-weight: 500;
    color: #F7F8FA;
    span {
      font-weight: 800;
    }
  }
  &__text {
    font-size: 32px;
    line-height: 140%;
    font-weight: 500;
    color: #F7F8FA;
    margin-top: 50px;
    span {
      font-weight: 800;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    right: 0;
    width: 760px;
    img {
      width: 100%;
    }
  }
  &__btn {
    width: 422px;
    height: 60px;
    position: relative;
    z-index: 1;
  }
  &__info {
    display: none;
  }
  &__items {
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 45px;
  }
  &__item {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 45px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 20px;
      line-height: 120%;
      color: #F7F8FA;
      margin-left: 25px;
    }
  }
}
.main2 {
  .main__right {
    justify-content: start;
    align-items: start;
  }
  .main__btn {
    margin-top: 60px;
  }
}
.services {
  padding: 60px 0;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 45px 30px;
    margin-top: 30px;
  }
  &__info {
    font-size: 20px;
    line-height: 140%;
    color: #A6A6A6;
    margin-top: 40px;
    a:hover {
      color: #ff5757;
    }
  }
}
.service-card {
  &__top {
    position: relative;
    &:hover {
      .service-card__img img {
        transform: scale(1.2);
      }
    }
  }
  &__top-desc {
    position: absolute;
    z-index: 1;
    left: 20px;
    bottom: 20px;
    padding-right: 20px;
  }
  &__img {
    position: relative;
    padding-bottom: 62%;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(49, 69, 92, 0.80) 0%, rgba(49, 69, 92, 0.80) 100%);
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      transition: all 0.5s;
    }
  }
  &__icon {
    max-width: 56px;
    img {
      max-width: 100%;
    }
  }
  &__title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 700;
    color: #FFF;
    margin-top: 10px;
  }
  &__bottom {
    margin-top: 22px;
  }
  &__text {
    p {
      font-size: 16px;
      line-height: 140%;
      color: #000000;
    }
  }
}
.consultation {
  background-image: url("../img/consultation-bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 80px 0;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(49, 69, 92, 0.80) 0%, rgba(49, 69, 92, 0.80) 100%);
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    position: relative;
    z-index: 2;
  }
  &__left {
    width: calc(100% - 450px);
  }
  &__right {
    width: 420px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    margin-left: 30px;
  }
  &__title {
    max-width: 800px;
    font-size: 48px;
    line-height: 120%;
    font-weight: 800;
    color: #ffffff;
  }
  &__text {
    max-width: 575px;
    font-size: 24px;
    line-height: 140%;
    font-weight: 500;
    color: #ffffff;
    margin-top: 30px;
  }
  &__btn {
    width: 100%;
    height: 60px;
  }
}
.customers {
  padding: 80px 0 20px 0;
  &__cards {
    width: calc(100% + 30px);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    margin-top: 50px;
  }
  &__card {
    width: 210px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 30px 60px 0;
    img {
      width: 100%;
    }
  }
}
.contract {
  background: #F7F8FA;
  padding: 80px 0;
  overflow: hidden;
  &__title {
    position: relative;
    z-index: 3;
    .text36 {
      color: #000;
    }
    .text20 {
      color: #4E4D4D;
    }
  }
  &__items {
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
    position: relative;
    margin: 80px auto 0;
    &:after {
      content: '';
      position: absolute;
      top: 32px;
      left: 0;
      width: 100%;
      height: 2px;
      background: #000000;
    }
  }
  &__item {
    text-align: center;
    position: relative;
    z-index: 2;
    &:first-child {
      .contract__item-number:after {
        content: '';
        position: absolute;
        top: 0;
        right: 100%;
        width: 500px;
        height: 64px;
        background: #F7F8FA;
        pointer-events: none;
      }
    }
    &:last-child {
      .contract__item-number:after {
        content: '';
        position: absolute;
        top: 0;
        left: 100%;
        width: 500px;
        height: 64px;
        background: #F7F8FA;
        pointer-events: none;
      }
    }
    &-number {
      flex-shrink: 0;
      width: 64px;
      height: 64px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FF2B2B;
      border-radius: 50%;
      margin: 0 auto;
      position: relative;
      span {
        font-size: 40px;
        line-height: 105%;
        color: #FFF;
      }
    }
    &-title {
      font-size: 20px;
      line-height: 120%;
      font-weight: 700;
      color: #000;
      margin-top: 35px;
    }
  }
}


@media screen and (max-width: 1320px) {
  .main {
    &__left {
      width: 500px;
    }
    &__right {
      width: calc(100% - 530px);
    }
    &__title {
      font-size: 40px;
    }
    &__text {
      font-size: 24px;
    }
    &__img {
      position: static;
      width: 100%;
    }
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__item {
      margin-right: 0 !important;
    }
  }
  .services {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
      gap: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 230px 0 30px 0;
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 10px 0;
      .main__text {
        display: none;
      }
    }
    &__right {
      width: 100%;
      justify-content: start;
      align-items: start;
      .main__text {
        display: block;
        margin-top: 10px;
      }
    }
    &__img {
      max-width: 600px;
      margin: 0 auto;
    }
    &__btn {
      margin: 35px 0 0 0;
    }
    &__info {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 30px;
      &-icon {
        flex-shrink: 0;
        width: 37px;
        img {
          width: 100%;
        }
      }
      &-text {
        font-size: 16px;
        line-height: 120%;
        color: #ffffff;
        margin: 0 0 0 10px;
      }
    }
    &__item {
      justify-content: start;
      &-text {
        font-size: 16px;
      }
    }
  }
  .main2 {
    .main__left {
      margin-bottom: 30px;
    }
  }
  .services {
    padding: 30px 0 45px 0 !important;
    &__cards {
      grid-template-columns: repeat(3, calc(50% - 20px / 2));
      gap: 20px;
      margin-top: 20px;
    }
    &__info {
      font-size: 16px;
    }
  }
  .service-card {
    &__title {
      font-size: 16px;
    }
  }
  .consultation {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 80px 0 0 0;
      justify-content: start;
      align-items: start;
    }
    &__title {
      max-width: 100%;
      font-size: 40px;
      font-weight: 400;
      span {
        font-weight: 800;
      }
    }
    &__text {
      max-width: 100%;
      font-weight: 400;
      span {
        font-weight: 800;
      }
    }
    &__btn {
      width: 355px;
    }
  }
  .customers {
    &__cards {
      width: calc(100% + 20px);
    }
    &__card {
      width: 150px;
      margin: 0 20px 40px 0;
    }
  }
  .contract {
    padding: 50px 0;
  }
}

@media screen and (max-width: 768px) {
  .contract {
    &__items {
      display: block;
      margin-top: 60px;
      &:after {
        position: absolute;
        top: 0;
        left: 32px;
        width: 2px;
        height: 100%;
      }
    }
    &__item {
      display: flex;
      align-items: center;
      text-align: start;
      &:not(:first-child) {
        margin-top: 80px;
      }
      &:first-child {
        .contract__item-number:after {
          content: '';
          position: absolute;
          top: -200px;
          left: 0;
          width: 100%;
          height: 200px;
          background: #F7F8FA;
          pointer-events: none;
        }
      }
      &:last-child {
        .contract__item-number:after {
          content: '';
          position: absolute;
          top: unset;
          left: 0;
          bottom: -200px;
          width: 100%;
          height: 200px;
          background: #F7F8FA;
          pointer-events: none;
        }
      }
      &-number {
        margin: 0;
      }
      &-title {
        margin: 0 0 0 30px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__btn {
      width: 100%;
    }
  }
  .services {
    &__cards {
      grid-template-columns: 100%;
      gap: 12px;
    }
  }
  .consultation {
    &__btn {
      width: 100%;
    }
  }
  .customers {
    &__card {
      width: 120px;
    }
  }
}

@media screen and (max-width: 360px) {
  .consultation {
    &__title {
      font-size: 32px;
    }
    &__text {
      font-size: 20px;
    }
  }
}