.wrap {
  overflow: hidden;
}
.btn-red {
  background: #FF2B2B;
  border: 1px solid #FF2B2B;
  span {
    font-size: 16px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #F7F8FA;
  }
  &:hover {
    background: #FF5757;
  }
}
.text36 {
  font-size: 36px;
  line-height: 140%;
  font-weight: 800;
  color: #4E4D4D;
}
.text20 {
  font-size: 20px;
  line-height: 140%;
  color: #A6A6A6;
}
.navigation {
  padding: 50px 15px;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li {
      font-size: 24px;
      line-height: 120%;
      a:hover {
        color: #ff5757;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .text36 {
    font-size: 24px;
  }
  .text20 {
    font-size: 16px;
  }
}