.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
}
.header2 {
  position: relative;
  .header-mob__menu-text {
    color: #31455C;
  }
  .header-mob__menu-burger {
    span {
      background: #31455C;
    }
  }
  .header-top__logo-text {
    color: #4E4D4D;
  }
  .header-top__info-text {
    color: #4E4D4D;
  }
  .header-top__number {
    a {
      color: #31455C;
    }
  }
  .header-top__btn {
    span {
      color: #31455C;
    }
  }
  .header-top__account {
    &-text {
      color: #4E4D4D;
    }
  }
  .header-bottom__list {
    border: 1px solid rgba(110, 125, 143, 0.80);
    background: #F7F8FA;
    backdrop-filter: blur(10px);
    li:not(:last-child) {
      border-right: 1px solid rgba(110, 125, 143, 0.80);
    }
  }
  .header-bottom__link {
    color: #273E51;
    &:hover {
      color: #ffffff;
    }
  }
  .active {
    color: #ffffff;
  }
  .header-bottom__item--active {
    .header-bottom__item-show {
      color: #ffffff;

    }
  }
  .header-bottom__item-show {
    color: #273E51;
  }
}
.header-mob {
  display: none;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header-top__account {
    margin-top: 0;
  }
  &__menu {
    display: flex;
    align-items: center;
    cursor: pointer;
    &-text {
      font-size: 16px;
      line-height: 120%;
      text-decoration-line: underline;
      color: #FFF;
    }
    &-burger {
      position: relative;
      width: 22px;
      height: 18px;
      margin-left: 12px;
      span {
        position: absolute;
        left: 0;
        width: 100%;
        height: 2px;
        background: #ffffff;
      }
      span:nth-child(1) {
        top: 0;
      }
      span:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
      }
      span {
        bottom: 0;
      }
    }
  }
}
.header-top {
  padding: 5px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__center {
    display: flex;
    align-items: center;
    margin: 0 15px;
  }
  &__logo {
    display: flex;
    align-items: center;
    &-img {
      width: 135px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      letter-spacing: 1.4px;
      text-transform: uppercase;
      color: #ffffff;
      margin-left: 14px;
    }
  }
  &__info {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 37px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      margin-left: 10px;
    }
  }
  &__numbers {
    margin-left: 30px;
  }
  &__number {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 120%;
    span {
      min-width: 130px;
      color: #888;
      text-align: end;
    }
    a {
      color: #ffffff;
      margin-left: 3px;
      &:hover {
        color: #FF5757;
      }
    }
  }
  &__btn {
    width: 267px;
    height: 45px;
    background: transparent;
    border: 1px solid #D9D9D9;
    text-align: center;
    span {
      font-size: 15px;
      line-height: 120%;
      text-transform: uppercase;
      color: #F7F8FA;
    }
    &:hover {
      border: 1px solid #FF2B2B;
      background: #FF2B2B;
      span {
        color: #F7F8FA;
      }
    }
  }
  &__account {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    &-icon {
      width: 21px;
      svg {
        width: 100%;
        height: auto;
        path {
          transition: all 0.5s;
        }
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
      text-decoration-line: underline;
      text-underline-offset: 3px;
      color: #ffffff;
      margin-left: 9px;
      transition: all 0.5s;
    }
    &:hover {
      .header-top__account-icon svg path {
        stroke: #FF5757;
      }
      .header-top__account-text {
        color: #FF5757;
      }
    }
  }
}
.header-bottom {
  &__top {
    justify-content: space-between;
    align-items: center;
    display: none;
  }
  &__close {
    font-size: 12px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      color: #FF0000;
    }
  }
  .header-top__account {
    margin-top: 0;
  }
  &__btn {
    width: 267px;
    height: 45px;
    margin: 50px auto 0;
    display: none;
  }
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid rgba(247, 248, 250, 0.15);
    background: rgba(0, 0, 0, 0.10);
    backdrop-filter: blur(10px);
    li:not(:last-child) {
      border-right: 1px solid rgba(247, 248, 250, 0.15);
    }
  }
  &__link {
    width: calc(100% / 5);
    height: 58px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFF;
    font-family: 'Inter';
    font-size: 15px;
    line-height: 120%;
    text-transform: uppercase;
    cursor: pointer;
    transition: al 0.3s;
    &:hover {
      background: #FF0000;
      backdrop-filter: blur(10px);
    }
  }
  .active {
    background: #F00;
    backdrop-filter: blur(10px);
  }
  &__item {
    width: calc(100% / 5);
    position: relative;
    &--active {
      .header-bottom__item-show {
        background: #F00;
        backdrop-filter: blur(10px);
      }
      .header-bottom__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      width: 100%;
      height: 58px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #FFF;
      font-family: 'Inter';
      font-size: 15px;
      line-height: 120%;
      text-transform: uppercase;
      cursor: pointer;
      transition: al 0.3s;
    }
    &-hidden {
      position: absolute;
      z-index: 4;
      top: 100%;
      left: 0;
      width: 279px;
      background: rgba(0, 0, 0, 0.8);
      border: 1px solid rgba(247, 248, 250, 0.15);
      border-top: unset;
      backdrop-filter: blur(10px);
      padding: 15px 10px;
      transition: all 0.3s;
      opacity: 0;
      pointer-events: none;
    }
    &-link {
      display: block;
      font-family: 'Inter';
      font-size: 15px;
      line-height: 120%;
      text-transform: uppercase;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 15px;
      }
      &:hover {
        color: #FF5757;
      }
    }
  }
  .header-top__info {
    display: none;
  }
}

@media screen and (max-width: 1300px) {
  .header-top {
    &__number {
      .number-info {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header-top {
    &__logo {
      &-text {
        display: none;
      }
    }
    &__btn {
      width: 240px;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 30px 0 15px;
  }
  .header2 {
    .header-mob {
      .header-top__account:hover {
        .header-top__account-text {
          color: #ff5757;
        }
      }
      .header-top__account-text {
        color: #31455C;
      }
    }
    .header-top__info-text {
      color: #ffffff;
    }
    .header-top__account {
      &-text {
        color: #ffffff;
      }
    }
  }
  .header-mob {
    display: block;
  }
  .header-top {
    padding: 15px 0 5px 0;
    &__right {
      display: none;
    }
    &__center {
      margin: 0 0 0 15px;
    }
    &__info {
      display: none;
    }
    &__numbers {
      margin-left: 0;
    }
  }
  .header-bottom {
    position: fixed;
    z-index: 10;
    top: 0;
    left: -105%;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(10, 38, 62, 0.90) 0%, rgba(10, 38, 62, 0.90) 100%);
    padding: 30px 0;
    overflow-y: auto;
    transition: all 0.5s;
    &--active {
      left: 0;
    }
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #ffffff;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #FF0000;
      border-radius: 3px;
    }
    &__top {
      display: flex;
    }
    &__list {
      display: block;
      border: unset !important;
      background: transparent !important;
      margin-top: 50px;
      li:not(:first-child) {
        margin: 15px 0 0 0;
      }
      li:not(:last-child) {
        border: unset !important;
      }
    }
    &__link {
      display: none;
    }
    &__item {
      width: auto;
      &--active {
        .header-bottom__item-show {
          background: transparent;
        }
      }
      &-show {
        height: auto;
        justify-content: start;
        font-size: 20px;
        color: #FF2B2B;
        cursor: unset;
        &:hover {
          background: transparent;
        }
      }
      &-hidden {
        position: static;
        opacity: 1;
        pointer-events: all;
        width: 100%;
        background: transparent;
        border: unset;
        padding: 0;
        margin-top: 15px;
      }
      &-link {
        text-transform: unset;
      }
    }
    .header-top__info {
      display: flex;
      flex-direction: column;
      margin-top: 50px;
      &-text {
        margin: 10px 0 0 0;
        br {
          display: none;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header-top {
    &__number {
      flex-direction: column;
      &:not(:first-child) {
        margin-top: 10px;
      }
      a {
        order: -1;
        margin: 0 0 5px 0;
      }
    }
  }
}

@media screen and (max-width: 360px) {
  .header-top {
    &__logo {
      width: 100px;
    }
  }
}